import React, { Component } from 'react';
import '../../styles/add.css';
import { Button, Grid, Col, Row } from 'react-bootstrap';

class AddTransaction extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: '',
      amount: '',
      description: '',
      category: ''
    };

  }

  render() {
    return (
      <div className="Add-Container">
        <Grid style={{padding: "0px"}}>
          <Row>
            <Col xs={12} className="add-row-title add-row">  
              <h1>
                  Add Transaction
              </h1>
            </Col>
          </Row>

          <Row className="add-row-mainRow add-row">
            <Col xs={3}>  
              <h3>Add</h3>
              <h2>SGD</h2>
            </Col>
            <Col xs={9}>  
              <span id="transaction-amt">0</span>
            </Col>
          </Row>

          <Row className="add-row-form add-row">
            <Col xs={12}>  
              <input type="text" className="form-control form-notes" id="notes" placeholder="description" />
            </Col>
          </Row>

          <Row className="add-row-form add-row">
            <Col xs={6}>  
            <input type="text" className="form-control form-date" id="date" placeholder="DD/MM" />

            </Col>
            <Col xs={6}>  
            <input type="text" className="form-control form-time" id="time" placeholder="HH:MM" />
            </Col>
          </Row>

          <Row className="add-row-category add-row">
            <Col xs={4}>
              <Button type="button" className="btn btn-active btn-outline-secondary">Food</Button>
            </Col>

            <Col xs={4}>
              <Button type="button" className="btn btn-outline-secondary">Transport</Button>
            </Col>

            <Col xs={4}>
              <Button type="button" className="btn btn-outline-secondary">Holidays</Button>
            </Col>
          </Row>

        <Row className="add-row-category add-row">
            <Col xs={4}>
              <Button type="button" className="btn btn-outline-secondary">Activities</Button>
            </Col>

            <Col xs={4}>
              <Button type="button" className="btn btn-outline-secondary">Shopping</Button>
            </Col>

            <Col xs={4}>
              <Button type="button" className="btn btn-outline-secondary">Others</Button>
            </Col>
          </Row>

          <Row style={{marginTop: "15px"}}>
            <Col xs={9}>  
              <Row className="add-row add-row-calculator">
                <Col xs={4}><p>7</p></Col>
                <Col xs={4}><p>8</p></Col>
                <Col xs={4}><p>9</p></Col>
              </Row>
              <Row className="add-row add-row-calculator">
                <Col xs={4}><p>4</p></Col>
                <Col xs={4}><p>5</p></Col>
                <Col xs={4}><p>6</p></Col>
              </Row>
              <Row className="add-row add-row-calculator">
                <Col xs={4}><p>1</p></Col>
                <Col xs={4}><p>2</p></Col>
                <Col xs={4}><p>3</p></Col>
              </Row>
              <Row className="add-row add-row-calculator">
                <Col xs={4}><p>.</p></Col>
                <Col xs={4}><p>0</p></Col>
                <Col xs={4}><p>-</p></Col>
              </Row>
            </Col>
            <Col xs={3}>
              <div className="add-row add-row-templates">
                <div>T1</div>
                <div>T2</div>
                <div>T3</div>
              </div>
              <div className="add-row add-row-calculator">
                <div><p>Add</p></div>
              </div>
            </Col>
          </Row>

        </Grid>
      </div>
    );
  }
}

export default AddTransaction;
